.chat-box {
  background: #fbfbfb;
  height: 100vh; }
  .chat-box .chat-box__header {
    background: white;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.14), 0 2px 4px rgba(0, 0, 0, 0.28);
    border-bottom-right-radius: 26px;
    border-bottom-left-radius: 26px;
    overflow: hidden; }
    .chat-box .chat-box__header .header__home-icon {
      position: absolute;
      left: 0;
      height: 28px;
      margin-left: 12px; }
    .chat-box .chat-box__header img:not(.header__home-icon) {
      height: 48px; }
    .chat-box .chat-box__header .header__operator {
      height: 52px;
      color: white;
      background: #ff8e9d; }
  .chat-box .chat-box__container {
    height: calc(100vh - 92px - 52px - 50px); }
  .chat-box .chat-box__footer .input-box {
    width: 100%;
    height: 52px;
    border-radius: 20px;
    font-size: 16px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    outline: none;
    background: rgba(0, 0, 0, 0.03); }
    .chat-box .chat-box__footer .input-box:focus {
      background: white; }

.simple-message {
  max-width: 360px;
  background: white;
  box-shadow: 2px 2px 15px 0px rgba(0, 0, 0, 0.05);
  border-radius: 8px; }
  .simple-message .user-image {
    height: 30px;
    width: 30px;
    padding: 3px;
    border-radius: 50%;
    overflow: hidden;
    border: 2px solid #ff8e9d;
    background: rgba(0, 0, 0, 0.03);
    margin-left: -18px; }

.drop-box-holder {
  width: 320px;
  height: 120px;
  border-radius: 4px;
  background: #ffdbdf; }
  .drop-box-holder .drop-box {
    border-radius: 4px;
    border: 1px dotted #ff8e9d; }

.document-list-container {
  background: #fbfbfb; }
  .document-list-container .document-header {
    height: 56px;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.14), 0 2px 4px rgba(0, 0, 0, 0.28); }
    .document-list-container .document-header input {
      border: none;
      outline: none;
      font-size: 16px;
      background: transparent; }
    .document-list-container .document-header ::-webkit-input-placeholder {
      color: white;
      opacity: 1;
      /* Firefox */ }
    .document-list-container .document-header ::-moz-placeholder {
      color: white;
      opacity: 1;
      /* Firefox */ }
    .document-list-container .document-header :-ms-input-placeholder {
      color: white;
      opacity: 1;
      /* Firefox */ }
    .document-list-container .document-header ::-ms-input-placeholder {
      color: white;
      opacity: 1;
      /* Firefox */ }
    .document-list-container .document-header ::placeholder {
      color: white;
      opacity: 1;
      /* Firefox */ }
    .document-list-container .document-header :-ms-input-placeholder {
      /* Internet Explorer 10-11 */
      color: white; }
    .document-list-container .document-header ::-ms-input-placeholder {
      /* Microsoft Edge */
      color: white; }
  .document-list-container .document-list {
    margin-top: 5px;
    height: calc(100vh - 56px); }
  .document-list-container .document-card {
    border-radius: 4px; }
    .document-list-container .document-card img {
      height: 48px; }

.sidenav {
  width: 260px;
  background: #f1f0f0;
  position: absolute;
  overflow: hidden;
  left: 0;
  top: 0;
  bottom: 0;
  height: 100vh !important;
  z-index: 3;
  -webkit-transition: width 300ms ease;
  transition: width 300ms ease; }
  .sidenav .sidenav__header {
    background: white; }
    .sidenav .sidenav__header img {
      height: 48px; }
  .sidenav .sidenav__links div {
    min-width: 260px;
    margin-bottom: 1px;
    padding: 20px 24px;
    background: white; }

.sidenav__overlay {
  position: fixed;
